<template>
  <div id="manage_label">
       <navbardomain></navbardomain>
       </br>   
      
       <div class="content">  
              <div id="joballocate"  > 
 <div class="alloca" id="alloca"> 
   <div>
     <h5><b></b></h5>
     </div>

  <div class="row"> 
     <div class="leftcont">      

   <b-form  inline @submit="onSubmit">          

      <label class="mr-sm-2" for="input-3"></label>
    <b-form-select
      class="mb-2 mr-sm-4 mb-sm-0"
      v-model="form.job"
      :value="null"
      :options="jobs"
      id="input-3"
      style="font-size:13px"
    >
      <option slot="first" :value="null" >Choose...</option>
    </b-form-select>  
  
    <b-button type="submit" variant="primary" style="font-size:13px" ></b-button>  &nbsp;

    <!-- <b-button type="button" variant="info" class="ml-auto" style="float:right;font-size:13px" v-b-modal.modal-create ></b-button> -->
  
  </b-form> 

</br>      
</br> 

<div class="table-responsive table-hover table-sm" style="height:auto;">
             <table class="table">
            
  <thead>
    <tr>
      <th scope="col"></th>
      <th scope="col" style="width:30%;text-align:center"></th>
      <th scope="col"></th>
      <th scope="col"></th> 
      <th scope="col" ></th>
    </tr>
  </thead>
  <tbody>   
    <tr v-for="item in items">
        <th scope="row">{{item.id}}</th>
        <td style="width:40%;text-align:center">{{item.name}}</td>
        <td>{{item.job}}</td>
        <td>{{item.domain}}</td>
        <td> <b-button variant="danger" style="font-size:13px;margin:auto;" v-b-modal.modal-delete @click="deletelabel(item.label_id)">delete</b-button></td>
    </tr>

     <b-modal id="modal-delete" title="" @ok="label_delete()" style="overflow:scroll;">

  <h6></h6>    
       
  </b-modal>
  
  </tbody>
            </table>
                </div>   

                <div>
        <nav aria-label="Page navigation example">
  <ul class="pagination justify-content-center">
    <li class="page-item"><a class="page-link" @click="pag('first')"></a></li>      
    <li v-for="n in limitpage"  v-if = "(currentpage+n)>0&&(currentpage+n)<=maxpage"  @click="pag(currentpage + n)"><span class="page-link" :style = "{color : n != 0 ? 'grey' : 'red'}">{{currentpage + n}}</span></li>
    <li class="page-item"><a class="page-link" @click="pag('end')"></a></li>
  </ul>
</nav>
</div>
    </div> 
       <div class="rightcont">  
         <div>
     <h5><b></b></h5>
     </div>
     </br>
     
     <div>
         <b-form-group id="input-group-1" label="" label-size="sm" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="new_label_name"
          required
          placeholder=""
        ></b-form-input>
      </b-form-group>         

      <b-form-group id="input-group-3" label="" label-size="sm" label-for="select-3">
       <b-form-select id="select-3" v-model="selected_2"  :options="jobs" >      
      <template v-slot:first>
        <option :value="null" disabled>--  --</option>
      </template>   
    </b-form-select>    
      </b-form-group>  

      <p></p>
        <input  class="jscolor" id="background_color" @change="update()" value="ab2567">

        </br></br>
      
        <p></p>
      <input class="jscolor" id="text_color" @change="update()" value="cc66ff"> 

      </br></br>

        <p></p>

         <button id="show_button" style="font-size:18px;background-color:#ab2567;color:#cc66ff"></button> 

        </br>   </br>   

        <b-button type="button" variant="info" style="font-size:13px" @click="label_create()"></b-button>

       </div>

                
        </div>
     </div>
     </div>
     </div>
       </div>
  </div>

 
</template>


<script>

import configs from './configs.vue'
import { colorpicker } from "../../colorpicker/jscolor.js"
// import ColorPicker from 'vue-bootstrap-colorpicker'
export default {
  name: 'manage_label', 
   data() {
      return {
          path:configs.path,
          form:{
              job:null,              
          },    
          
          jobs:[],   
          limitcontent:13,
          items: [],
          currentpage:1,
          maxpage:0,
          limitpage:[-2,-1,0,1,2],
          
          domain:window.localStorage.getItem('domain_id'),
          

          label_delete_id:"",

          new_label_name:"",        
          selected_2:null,

      

      }
    },

      mounted(){

        colorpicker();

        var ID=1 
        var paper = []
        this.$http.get(this.path+"job/label/?domain="+this.domain+"&limit="+this.limitcontent,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);      
            var pag =Math.ceil(response.body.count / this.limitcontent) ;             
           console.log("maxpage",pag);
             this.maxpage = pag;          
          response.body.results.forEach(element => {   
                 paper.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.name,job:element.job_name,job_id
                 :element.job,domain:element.domain_name,label_id:element.id});
                 ID=ID+1;             
          });
          this.items=paper;
          console.log("papers",this.items);

          },function(){
alert('');
});     

   this.$http.get(this.path+"job/job/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);
       
          var fie=[]
          response.body.forEach(element => {
            fie.push({text:element.name,value:element.id})
          });

          this.jobs=fie;         

          },function(){
alert('');
});
},

 methods: {  

    update(){              
        var color=document.getElementById('background_color').value;
        var textcolor=document.getElementById('text_color').value;
        document.getElementById('show_button').style.backgroundColor="#"+color;
        document.getElementById('show_button').style.color="#"+textcolor;
        // console.log(document.getElementById('background_color').value)
        // console.log(document.getElementById('show_button').style.backgroundColor)
    },
    

      onSubmit(evt) {
        evt.preventDefault()
        console.log(JSON.stringify(this.form))
     
        var ID=1 
        var paper = []
        this.$http.get(this.path+"job/label/?domain="+this.domain+"&job="+this.form.job+"&limit="+this.limitcontent,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log("search results",response);      
            var pag =Math.ceil(response.body.count / this.limitcontent) ;             
           console.log("maxpage",pag);
             this.maxpage = pag;          
          response.body.results.forEach(element => {   
                paper.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.name,job:element.job_name,job_id
                 :element.job,domain:element.domain_name});
                 ID=ID+1;             
          });
          this.items=paper;
          console.log("papers",this.items);

          },function(){
alert('');
});
      },

       pag(data){
            console.log(this.currentpage);
            var off=0;
            var ID=1;
            if(data=="first"){
                off=0;
                this.currentpage=1;
            }else if(data == "end"){
                off=(this.maxpage-1)*this.limitcontent;
                this.currentpage=this.maxpage;
            }else{
                off = (data-1)*this.limitcontent;
                this.currentpage = data;
            }
            var pap = [];
            
            if(this.form.job == null){
              var path=this.path+"job/label/?domain="+this.domain+"&limit="+this.limitcontent+"&offset="+off;
            }else{
              var path = this.path+"job/label/?domain="+this.domain+"&job="+this.form.job+"&limit="+this.limitcontent+"&offset="+off;
            }

              this.$http.get(path,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {      
                response.body.results.forEach(element => {       
                pap.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.name,job:element.job_name,job_id
                 :element.job,domain:element.domain_name});
                 ID=ID+1;              
          });
           this.items=pap;              
            console.log("papers",this.items);    
            },function(){
                 alert("");
      });           
        },

        deletelabel:function(data){
          this.label_delete_id=data;
        },

        label_delete(){
          var data=this.label_delete_id;
          this.$http.delete(this.path+"job/label/"+data,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{           
            this.pag(this.currentpage);
          },function(){
            alert("");
          })
        },

        label_create(){
          this.$http.post(this.path+"job/label/",{name:this.new_label_name,shortcut:"",background_color:"#"+document.getElementById('background_color').value,text_color: "#"+document.getElementById('text_color').value,domain:this.domain,job:this.selected_2},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{           
            this.pag(this.currentpage);
          },function(){
            alert("");
          })

        }
 } 


}

</script>

<style>
.content{   
    width:90%;
    height:100%;    
    margin:auto;
}

.joballocate{
  height:100%;
  width:100%;
}

.alloca{
  background-color:white;
  height:100%;
  width:100%;
  min-height:800px;
  padding:2%  
}

.leftcont{
  width:60%;
   padding-left:2%;
   padding-top:1%;
  
   margin-right:5%;
}

.rightcont{
   width:35%;
   padding-left:5%;
   padding-top:1%;
border-left:grey solid
}

/*.three{
     width:25%;
   padding-left:2%;
   padding-top:1%
} */



</style>



