<script>
import configs from './configs.vue'
export default {
  name: 'allocate', 
   data() {
      return {
         path:configs.path,

        //  form: {        
        //   subject: null,
        //   field:null,
        //   job:null, 
          
        // },

        subject:window.localStorage.getItem('user_subject'),
        domain:window.localStorage.getItem('domain'),
        job:window.localStorage.getItem('job'),

        project_user_list:[],
        user_list:[],
        
        selected:null,       
        new_user_name:"",

        papers:[],
        limitcontent:15,
        maxpage:0,
        left:0,

        currentpage:1,
        limitpage:[-2,-1,0,1,2],
 
         bool_entity:false,
      // bool_entity:true,
      bool_relation:false,

       
        show: true,
       
      
      
        user:[],
       num_task:0,
       number:0,

      bool_entity:window.localStorage.getItem('entity'),
      bool_relation:window.localStorage.getItem('relation'),

      }
    },
   
    methods: {    
      // onSubmit(evt) {
      //    evt.preventDefault();
      //    console.log(JSON.stringify(this.form));
      //   //  
         
        
        
      // },

      fresh(){
        //  var dispatch=[];
        //   this.$http.get(this.path+"job/dispatched/?job="+this.form.job,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{          
        //   response.body.forEach(element => {           
        //     dispatch.push(element.paper);
        //   });
        //   this.patched=dispatch;
        //   console.log("dispatched",dispatch);
        // });        
         var Input = document.getElementsByTagName("input");   
          var num = (this.currentpage==this.maxpage) ? (this.left+1):(this.limitcontent+1);
          for(var i=0;i<num;i++){
              Input[i].checked=null;
            }
      },

       pag(data){
            this.fresh();
            console.log(this.currentpage);
            var off=0;
            var ID=1;
            if(data=="first"){
                off=0;
                this.currentpage=1;
            }else if(data == "end"){
                off=(this.maxpage-1)*this.limitcontent;
                this.currentpage=this.maxpage;
            }else{
                off = (data-1)*this.limitcontent;
                this.currentpage = data;
            }
            var pap = [];
              var path = this.path+"paper/paperundispatched/?project_id="+window.localStorage.getItem('project_id')+"&domain"+this.domain+"&limit="+this.limitcontent+"&offset="+off;
              this.$http.get(path,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
              
            response.body.results.forEach(element => {
            
                 pap.push({id:(this.currentpage-1)*this.limitcontent+ID,paperid:element.id,title:element.paper_title,status:false});
                 ID=ID+1;
           
           });
           this.papers=pap;              
            console.log("",this.papers);    
            },function(){
                 alert("");
      });           
        },

        choose_allocate(){
          console.log("user",this.selected);
          this.number=0;
        if(this.selected==null){
          alert("");
        }else{
          console.log(this.selected);
          // console.log($("input[type='checkbox']:checked").length);
          var num_tasks = 0;
          // this.num_task=$("input[type='checkbox']:checked").length;

             var Input = document.getElementsByTagName("input");  
             var num = (this.currentpage==this.maxpage) ? (this.left+1):(this.limitcontent+1);
             console.log("num",num);
             for(var i =1;i<num;i++){
            if(Input[i].checked == true){
              console.log("selectpaper",Input[i].id);
              this.$http.get(this.path+"job/project_user/Dispatchjob/?user_id="+this.selected+"&paper_id="+Input[i].id+"&project_id="+window.localStorage.getItem('project_id'),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
                  console.log(response);
                this.number=this.number+1;
              },function(){
                alert("")
              })
              num_tasks = num_tasks +1;
           }
          }; 
          this.num_task=num_tasks; 
         this.pag(this.currentpage);         
         } 
        },

        choose_delete(){
          console.log("user",this.selected);
          this.number=0;
        if(this.selected.length == 0){
          alert("");
        }else{
          var id = 0
          this.project_user_list.forEach(element=>{
            if(element.value == this.selected){
              id = element.id;
               this.$http.delete(this.path+"job/projectrole/"+id+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
               this.project_user_list=this.project_user_list.filter(function(item){
                   return item.value != element.value;
            });
            alert("")
          },function(){
            alert("")
          });
          return false;    
            }
          })
                
            
         } 

        },

        add_member(){

          var new_name=this.new_user_name;

            console.log(new_name);
            var exist_project= false;           

            this.project_user_list.forEach(element => {
              if(element.text == this.new_user_name){
                alert("");
                exist_project =true;
                return false;
              }
            });            
            
            var exists = this.user_list.filter(function(item){
              console.log(item);
              return item.text == new_name;
            })

            if(exists.length == 0){
              alert("")
            }else{
              if(exist_project == false){
              this.user_list.forEach(element => {
                if(element.text == this.new_user_name){
                  this.$http.post(this.path+"job/projectrole/",{project:parseInt(window.localStorage.getItem('project_id')),user:element.value},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
                      this.project_user_list.push(element);
                      alert("")
                  },function(){
                    alert("")
                  });
                 
                }
              });            
              this.new_user_name="";
            }
            }        

           
            
        },
    },    

