//             fie.push({text:element.name,value:element.id})
//           });
//           fie.push({text:"",value:""});
//           this.fields=fie;         
//           },function(){
// alert('');
// });

     this.$http.get(this.path+"user/user/?role=0",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);
       
          var users=[]
          response.body.forEach(element => {
            users.push({text:element.username,value:element.id})
          });          
          this.user_list=users;         
          },function(){ 
alert('');
});

    this.$http.get(this.path+"job/projectrole/?project="+window.localStorage.getItem('project_id'),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);
       
          var users=[]
          // var users_list=[]
          // var not_in_list=[]
          // var in_list =false
          response.body.forEach(element => {            
            users.push({value:element.user,text:element.username,id:element.id})
          });          

          // this.user_list.forEach(element => {

          //   users.forEach(element2 => {
          //     if(element.value==element2){               
          //       users_list.push(element);
          //       in_list = true
          //     }
          //   });

          //   if(in_list != true){
          //     not_in_list.push(element);
          //   }else{
          //      in_list=false
          //   }       

          // });

          // this.not_in_user_list=not_in_list;
          this.project_user_list=users;   

          // console.log(this.not_in_user_list);
          // console.log(this.project_user_list);      
          },function(){ 
alert('');
});

// 
        var pap = [];
        var ID=1;      
        this.$http.get(this.path+"paper/paperundispatched/?project_id="+window.localStorage.getItem('project_id')+"&domain="+this.domain+"&limit="+this.limitcontent,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
          console.log("dispatch",response);
          var pag =Math.ceil(response.body.count / this.limitcontent) ;     
          this.left = response.body.count % this.limitcontent;        
           console.log("maxpage",pag);
             this.maxpage = pag;          
          response.body.results.forEach(element => {                  
                 pap.push({id:(this.currentpage-1)*this.limitcontent+ID,paperid:element.id,title:element.paper_title,status:false});
                 ID=ID+1;             
          });
          this.papers=pap;
          console.log("papers",this.papers);
        },function(){
          alert("");
        });

        // this.pag("first");

        // 
         var Input = document.getElementsByTagName("input");   
         var limit = this.limitcontent;
          Input[0].onclick = function(){
            console.log("check",limit);
           for(var i=1;i<limit+1;i++){                   
                   if(Input[i].disabled==true){                     
                   }else{
                     Input[i].checked=this.checked;
                   }
              
            }
          };  






},


}

</script>

