.content{   
    width:90%;
    height:100%;    
    margin:auto;
}

.joballocate{
  height:100%;
  width:100%;
}

.alloca{
  background-color:white;
  height:100%;
  width:100%;
  min-height:800px;
  padding:2%  
}

.leftcont{
  width:60%;
   padding-left:2%;
   padding-top:1%;
  
   margin-right:5%;
}

.rightcont{
   width:35%;
   padding-left:5%;
   padding-top:1%;
border-left:grey solid
}

.three{
     width:25%;
   padding-left:2%;
   padding-top:1%
}



</style>



