<template>
  <div id="superuser">
    <div>
    <b-navbar type="light" variant="white" text-variant="black">
     <b-navbar-nav>
      <b-navbar-brand><img src="../../static/logo.png" aria-hidden="true" style="height:30px"></img> <b></b></b-navbar-brand>
    </b-navbar-nav>

     <b-navbar-nav class="ml-auto">
        <b-button variant="secondary" pill size="sm" class="my-sm-0" @click="quit()" > {{username}} &nbsp;  </b-button>
      </b-navbar-nav>        
  </b-navbar>
</div>

  <div class="row content">   

  <div id="subject_content">
   
 <div style="width:100%;text-align:center;">
       <h4>
        
         </h4>
       </br>
        {{information}}   

        <div style="overflow-y:scroll;max-height:500px">
        <b-list-group style="width:90%;margin:auto" horizontal v-for="subject in subject_list" :key="subject.value">
  <b-list-group-item button >{{subject.text}}</b-list-group-item>
  <b-list-group-item button @click="bianji_information(subject.value)" style="width:40%" ><b-button size="sm" v-b-modal.modal-bianji variant="warning" >   </b-button></b-list-group-item>  
  <b-list-group-item button @click="delete_subject(subject.value)" style="width:40%" ><b-button size="sm" variant="danger" v-b-modal.modal-delete>   </b-button></b-list-group-item>  

</b-list-group>
</div>

</br>

<!-- <b-button variant="warning" v-b-modal.modal-bianji  v-show="user_role == 1">   </b-button>  &nbsp;&nbsp; -->
 <b-button variant="info" v-b-modal.modal-create >   </b-button>

 <b-modal id="modal-create" title="" @ok="create_subject()">
    
       <b-form-group id="input-group-1" label="" label-size="sm" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="new_subject_name"
          required
          placeholder=""
        ></b-form-input>
      </b-form-group>        

  </b-modal>

   <b-modal id="modal-bianji" title="" @ok="bianji_subject()">  
    
       <b-form-group id="input-group-4" label="" label-size="sm" label-for="input-4">
        <b-form-input
          id="input-4"
          v-model="subject_name"
          required        
        ></b-form-input>
      </b-form-group>        

  </b-modal> 

  <b-modal id="modal-delete" title="" @ok="subject_delete()">  
    
       <h6></h6>   

  </b-modal> 
      </div>            
      <div>        
        </div>   
      </div>

  <div id="job_content" >

 <div style="width:100%;text-align:center;">
       <h4>        
         </h4>
       </br>
        {{information2}}   

        <div style="overflow-y:scroll;max-height:500px">
        <b-list-group style="width:90%;margin:auto" horizontal v-for="admin in admin_list" :key="admin.value">
  <b-list-group-item button>{{admin.text}}</b-list-group-item>
  <b-list-group-item button>{{admin.subject}}</b-list-group-item> 
  <b-list-group-item button @click="delete_admin(admin.value)" style="width:40%" ><b-button size="sm" variant="danger" v-b-modal.modal-delete-admin >   </b-button></b-list-group-item>  

</b-list-group>
</div>

</br> 
 <b-button variant="info" v-b-modal.modal-create-admin >   </b-button>

   <b-modal id="modal-create-admin" title="" @ok="add_admin()">     

      <b-form-group id="select1" label="" label-for="select-1">
       <b-form-select id="select-1" v-model="selected"  :options="user_list" size="sm">      
      <template v-slot:first>
        <option :value="null" disabled>--  --</option>
      </template>   
    </b-form-select>    
      </b-form-group>   

       <b-form-group id="select2" label="" label-for="select-2">
       <b-form-select id="select-2" v-model="selected_subject"  :options="subject_list" size="sm">      
      <template v-slot:first>
        <option :value="null" disabled>--  --</option>
      </template>   
    </b-form-select>    
      </b-form-group>   
     
  </b-modal> 

     <b-modal id="modal-delete-admin" title="" @ok="admin_delete()">     

 <h6></h6>        
     
  </b-modal> 
      </div>            
      <div>        
        </div>   
      </div> 
      </div>

  </div>
</template>

<script>

import configs from './configs.vue'
export default {
  name: 'superuser', 
   data() {  
      return {
        path:configs.path,
        information:"",
        information2:"",
        subject_list:[],
        // domin_list:[],
        admin_list:[],
         user_role:window.localStorage.getItem('user_role'),
         username: window.localStorage.getItem('user'),
         new_subject_name:"",           
         
         subject_name:"",        
         subjectid:"",  

         user_list:[],
         selected:null,
         selected_subject:null,

         subject_delete_id:"",
         admin_delete_id:"",
     
         
      }
    },

    watch:{     
     
    },

    methods: {     

        quit(){
          window.localStorage.removeItem('token');
          this.$router.push({path:'/'})
        },    

        bianji_information(data){
           this.$http.get(this.path+"paper/subject/"+data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);
           this.subject_name=response.body.name;     
           this.subjectid=data;      
          },function(){
            alert("")
          })
        },

        bianji_subject(){
          this.$http.put(this.path+"paper/subject/"+this.subjectid+"/",{id:this.subjectid,name:this.subject_name},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);       
           this.subject_list.forEach(element => {
             if(element.value==this.subjectid){
               element.text=this.subject_name;
             }
           });           
           
          },function(){
            alert("")
          })

        },

        create_subject(){
           console.log(this.new_subject_name);           
           this.$http.post(this.path+"paper/subject/",{name:this.new_subject_name},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
             //  console.log(response);
             this.subject_list.push({'text':this.new_subject_name,'value':response.body.id})            
           },function(){
              alert("")
           });
        },

        delete_subject(data){
          console.log(data);
          this.subject_delete_id=data
          
        },

        subject_delete(){
          var data=this.subject_delete_id;
          this.$http.delete(this.path+"paper/subject/"+ data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
            this.subject_list=this.subject_list.filter(function(item){
              return item.value !=  data
            });            
          },function(){
            alert("")
          })
        },    

        delete_admin(data){
          console.log(data);
          this.admin_delete_id=data;
         
        },

        admin_delete(){
          var data=this.admin_delete_id;
                this.$http.get(this.path+"user/user/"+data+"/del_teacher/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
            this.admin_list=this.admin_list.filter(function(item){
              return item.value != data
            });           
          },function(){
            alert("")
          })
        },

        add_admin(){
          console.log(this.selected);
          console.log(this.selected_subject);
           this.$http.get(this.path+"user/user/"+this.selected+"/set_teacher/?subject_id="+this.selected_subject,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
             console.log(response);


             this.user_list.forEach(element=>{
               if(element.value==this.selected){
                 this.admin_list.push({text:element.text,value:element.value,subject:response.body})

                 return false;
               }
             });     
          
            alert("")
          },function(){
            alert("")
          })

        }
     
    },

    mounted(){
      
          //

        this.$http.get(this.path+"paper/subject/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  if(response2.body.length==0){
                      this.information="";
                      console.log("")
                  }else{
                      var list=[];
                      console.log(response2.body);
                      response2.body.forEach(element => {
                          list.push({'text':element.name,'value':element.id})
                      });                      
                      this.subject_list=list;
                  }

              },function(){
                alert("");
              }); 


         
               this.$http.get(this.path+"job/job/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  //

                  if(response2.body.length==0){
                      this.information2="";
                  }else{
                         var job=[];
                      console.log(response2.body);
                      response2.body.forEach(element => {
                          job.push({'text':element.name,'value':element.id,'job_table':element.job_table})
                      });                      
                      this.job_list=job;            
                  }                    

              },function(){
                alert("");
              }); 


       this.$http.get(this.path+"user/user/?role=1",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);

          if(response.body.length==0){
            this.information2="";
          }else{
            var users=[]
          response.body.forEach(element => {
            users.push({text:element.username,value:element.id,subject:element.subjectname})
          });          
          this.admin_list=users;         
          }     
          
},function(){ 
alert('');
          });


       this.$http.get(this.path+"user/user/?role=0",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);

          if(response.body.length==0){
            this.information2="";
          }else{
            var users=[]
          response.body.forEach(element => {
            users.push({text:element.username,value:element.id})
          });          
          this.user_list=users;         
          }     
          
},function(){ 
alert('');
          });
    
             
    }


}
</script>

<style>

#subject_content{
  width:40%;
  height:auto;
 
  background-color:white;
   margin-left:6%;
  margin-top:3%;
  padding:30px;
  border-style:solid;
    border-width:2px;
}

#job_content{
  width:40%;
 
  height:auto;
   margin-left:3%;
   margin-right:5%;
  background-color:white;
  margin-top:3%;
  padding:30px;
  border-style:solid;
    border-width:2px;
}



</style>



