<template>
  <div id="navbar_domain">
 <b-navbar variant="white" text-variant="black">
  
       <b-navbar-brand @click="rout('project')"><img src="../../static/logo.png" aria-hidden="true" style="height:30px"></img> <b></b></b-navbar-brand>
       
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>

    <b-navbar-nav style="font-size:18px;width=100%">

      <b-nav-item-dropdown text="" right >
        <b-dropdown-item  @click="rout('addtext')"></b-dropdown-item>
        <b-dropdown-item  @click="rout('findtext')"></b-dropdown-item>       
      </b-nav-item-dropdown>   

    </b-navbar-nav>

     <b-navbar-nav style="font-size:18px;width=100%">
       <b-nav-item  @click="rout('managelabel')" > 
       </b-nav-item>
       </b-navbar-nav>

    <b-navbar-nav class="ml-auto">
        <b-button variant="dark" pill size="sm" class="my-sm-0" @click="rout('project')" >  </b-button>&nbsp;
         <b-button variant="secondary" pill size="sm" class="my-sm-0" @click="quit()" > {{username}}  </b-button> 
      </b-navbar-nav>            
  
   </b-collapse>

   </b-navbar>
</div>
</template>

<script>
import configs from './configs.vue'
  export default {
      name: 'navbar_domain',
    
      data(){
        return{
           path:configs.path,           
           username: window.localStorage.getItem('user'),              
        }
      },
      methods:{

        quit(){
          window.localStorage.removeItem('token');
          this.$router.push({path:'/'})
        },

        rout(data){
          this.$router.push({
            path:'/'+data,
          })
        },
      

      },
      mounted(){      
      }
    
  }
</script>

<style>


</style>


