<template>
  <div id="login">
    <div>
    <b-navbar type="light" variant="white" text-variant="black">
     <b-navbar-nav>
      <b-navbar-brand><img src="../../static/logo.png" aria-hidden="true" style="height:30px"></img> <b></b></b-navbar-brand>
    </b-navbar-nav>
  </b-navbar>
</div>

  <div id="login_box">
   
 <div style="width:100%;text-align:center;">
       <h4></h4>
      </div>
       
       </br>

      <div>
<b-form>    

      <b-form-group id="input-group-1" label="" label-size="sm" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="form.name"
          required
          placeholder="Enter name"
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2"  label="" label-size="sm" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.password"          
          required
          type="password"
          placeholder="Enter password"
        ></b-form-input>
      </b-form-group>

    </br>
    

      <b-button block  variant="secondary" @click="submit()" pill>  &nbsp;  </b-button>
      </br>
       <b-button block  variant="dark" @click="zhuce()" pill>  &nbsp;  </b-button>
       </br>
      <!-- <b-button type="reset" variant="danger">Reset</b-button> -->
    </b-form>

    
        </div>
    
  </div>
 

  </div>
</template>

<script>

import configs from './configs.vue'
export default {
  name: 'login', 
   data() {  
      return {
        path:configs.path,
        form: {          
          name: '',
          password: '',          
        },   
        
       
      }
    },


    methods: {
      //  
      submit() {
        
        window.localStorage.clear();       

       this.$http.post(this.path+"api-token-auth/",{"username":this.form.name,"password":this.form.password}).then((response) => {
// console.log(response);
          window.localStorage.setItem('Authorization',' Token  '+ response.body.token);    
          window.localStorage.setItem('user',this.form.name);

          this.$http.get(this.path+"user/user/?username="+window.localStorage.getItem(['user']),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{            
             console.log("",response);
             window.localStorage.setItem('user_id',response.body[0].id);
             window.localStorage.setItem("user_subject",response.body[0].subject);
             window.localStorage.setItem('user_role',response.body[0].role);

             if(response.body[0].role==2){
                this.$router.push({path:'/superuser'});
             }else{
               this.$router.push({path:'/project'});
             }

            

          },function(){
            alert('');
          });  

            console.log("localstorage",window.localStorage);                

},function(){
alert('');
});
        
          
      },

      //
      zhuce(){
        window.localStorage.clear();   
         this.$http.post(this.path+"user/reg/",{"username":this.form.name,"password":this.form.password}).then((response) => {
console.log(response);
          this.submit();
          // window.localStorage.setItem('Authorization',' Token  '+ response.body.token);    
          // window.localStorage.setItem('user',this.form.name);

          //  this.$http.get(this.path+"user/user/?username="+window.localStorage.getItem(['user']),{headers:{Authorization:response.body.token}}).then((response2)=>{            
          //   //  console.log("",response);
          //    window.localStorage.setItem('user_id',response2.body[0].id);
          //    window.localStorage.setItem("user_subject",response2.body[0].user_subject);
          //    window.localStorage.setItem('user_role',response2.body[0].user_role);

          //   this.$router.push({path:'/project'});

          // },function(){
          //   alert('');
          // });  
          // console.log( window.localStorage.getItem(['Authorization']));      
            //  this.$router.push({path:'/home'})   
},function(){
alert('');
});    

      },
     
    },

    created(){
          var _self = this;
            document.onkeydown = function(e){
                var key = window.event.keyCode;
                if(key == 13){
                    _self.submit();
                }
               
            }
      }
}
</script>

<style>

#login_box{
  width:400px;
  height:430px;
  margin:auto;
  background-color:white;
  margin-top:10%;
  padding:30px;
}

</style>



