<template>
  <div id="project">
    <div>
    <b-navbar type="light" variant="white" text-variant="black">
     <b-navbar-nav>
      <b-navbar-brand><img src="../../static/logo.png" aria-hidden="true" style="height:30px"></img> <b></b></b-navbar-brand>
    </b-navbar-nav>

     <b-navbar-nav class="ml-auto">
        <b-button variant="secondary" pill size="sm" class="my-sm-0" @click="quit()" > {{username}} &nbsp;  </b-button>
      </b-navbar-nav>        
  </b-navbar>
</div>

  <div class="row content">   

  <div id="project_content">
   
 <div style="width:100%;text-align:center;">
       <h4>
        
         </h4>
       </br>
        {{information}}   

        <div style="overflow-y:scroll;max-height:500px">
        <b-list-group style="width:90%;margin:auto" horizontal v-for="project in project_list" :key="project.value">
  <b-list-group-item button  @click="jump_project(project.value)" >{{project.text}}</b-list-group-item>
  <b-list-group-item button @click="bianji_information(project.value)" style="width:30%" v-show="user_role == 1"><b-button size="sm" v-b-modal.modal-bianji variant="warning" >   </b-button></b-list-group-item>  
  <b-list-group-item button @click="delete_project(project.value)" style="width:30%" v-show="user_role == 1"><b-button size="sm" variant="danger" v-b-modal.modal-delete>   </b-button></b-list-group-item>  

</b-list-group>
</div>

</br>

<!-- <b-button variant="warning" v-b-modal.modal-bianji  v-show="user_role == 1">   </b-button>  &nbsp;&nbsp; -->
 <b-button variant="info" v-b-modal.modal-create  v-show="user_role == 1">   </b-button>

 <b-modal id="modal-create" title="" @ok="create_project()">
    
       <b-form-group id="input-group-1" label="" label-size="sm" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="new_project_name"
          required
          placeholder=""
        ></b-form-input>
      </b-form-group>    

       <b-form-group id="input-group-2" label="" label-size="sm" label-for="select-2">
       <b-form-select id="select-2" v-model="selected_1"  :options="domin_list" >      
      <template v-slot:first>
        <option :value="null" disabled>--  --</option>
      </template>   
    </b-form-select>    
      </b-form-group>    

      <b-form-group id="input-group-3" label="
      " label-size="sm" label-for="select-3">
       <b-form-select id="select-3" v-model="selected_2"  :options="job_list" >      
      <template v-slot:first>
        <option :value="null" disabled>--  --</option>
      </template>   
    </b-form-select>    
      </b-form-group>          

  </b-modal>

   <b-modal id="modal-bianji" title="" @ok="bianji_project()">  
    
       <b-form-group id="input-group-4" label="" label-size="sm" label-for="input-4">
        <b-form-input
          id="input-4"
          v-model="project_name"
          required        
        ></b-form-input>
      </b-form-group>      
  </b-modal> 

   <b-modal id="modal-delete" title="" @ok="project_delele()">  
    
     <h6></h6> 
  </b-modal> 
      </div>            
      <div>        
        </div>   
      </div>

  <div id="domain_content" v-show="user_role == 1">
   
 <div style="width:100%;text-align:center;">
       <h4>{{subject}}        
         </h4>
       </br>
        {{information2}}   

        <div style="overflow-y:scroll;max-height:500px">
        <b-list-group style="width:90%;margin:auto" horizontal v-for="domain in domin_list" :key="domain.value">
  <b-list-group-item button @click="jump_domain(domain.value)">{{domain.text}}</b-list-group-item>
  <b-list-group-item button @click="bianji_domain_information(domain.value)" style="width:60%" v-show="user_role == 1"><b-button size="sm" v-b-modal.modal-bianji-domain variant="warning" >   </b-button></b-list-group-item>  
  <b-list-group-item button @click="delete_domain(domain.value)" style="width:60%" v-show="user_role == 1"><b-button size="sm" variant="danger" v-b-modal.modal-delete-domain >   </b-button></b-list-group-item>  

</b-list-group>
</div>

</br>

<!-- <b-button variant="warning" v-b-modal.modal-bianji  v-show="user_role == 1">   </b-button>  &nbsp;&nbsp; -->
 <b-button variant="info" v-b-modal.modal-create-domain  v-show="user_role == 1">   </b-button>

 <b-modal id="modal-create-domain" title="" @ok="create_domain()">
    
       <b-form-group id="input-group-7" label="" label-size="sm" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="new_domain_name"
          required
          placeholder=""
        ></b-form-input>
      </b-form-group>    
        

  </b-modal>

   <b-modal id="modal-bianji-domain" title="" @ok="bianji_domain()">  
    
       <b-form-group id="input-group-4" label="" label-size="sm" label-for="input-3">
        <b-form-input
          id="input-3"
          v-model="domain_name"
          required        
        ></b-form-input>
      </b-form-group>  
  </b-modal> 

   <b-modal id="modal-delete-domain" title="" @ok="domain_delete()">  
    
      <h6></h6> 
  </b-modal> 
      </div>            
      <div>        
        </div>   
      </div> 
      </div>

      

  </div>
</template>

<script>

import configs from './configs.vue'
export default {
  name: 'project', 
   data() {  
      return {
        path:configs.path,
        information:"",
        information2:"",
        project_list:[],
        domin_list:[],
        job_list:[],
         user_role:window.localStorage.getItem('user_role'),
         username: window.localStorage.getItem('user'),
         new_project_name:"",
         selected_1:null,
         selected_2:null,       
         
         project_name:"",
         project_domin:"",
         project_job:"",
         projectid:"",  
         project_delete_id:"",
         domain_delete_id:"",
                  
         subject:"",

         domain_name:"",
         new_domain_name:"",
         domain_id:"",
     
         
      }
    },

    watch:{     
     
    },

    methods: {
        jump_project(data){
            console.log(data);
            window.localStorage.setItem('project_id',data);
           
            window.localStorage.setItem('entity',false);
            window.localStorage.setItem('relation',false);
            // console.log("click",window.localStorage.getItem(['project_id']));

            this.$http.get(this.path+"job/job/?project="+window.localStorage.getItem(['project_id']),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
              console.log("",response);
              if(response.body.length==0){
                alert("");
              }else{       
                
                this.$http.get(this.path+"job/project/"+window.localStorage.getItem(['project_id'])+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
                  window.localStorage.setItem('domain',response.body.domain);
                  window.localStorage.setItem('job',response.body.job);
                });
                
                  if(response.body[0].job_table=="job_relation"){
                    window.localStorage.setItem('relation',true)
                      this.$router.push({path:'/relation'})   
                  }else if(response.body[0].job_table=="job_entity"){
                    window.localStorage.setItem('entity',true)
                       this.$router.push({path:'/entityannoation'})   
                  }               
                
              }

            },function(){
              alert("")
            })

        },

        jump_domain(data){
          window.localStorage.setItem('domain_id',data);
          this.$router.push({path:'/findtext'})

        },

        quit(){
          window.localStorage.removeItem('token');
          this.$router.push({path:'/'})
        },    

        bianji_information(data){
           this.$http.get(this.path+"job/project/"+data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);
           this.project_name=response.body.name;
           this.project_domin=response.body.domain;
           this.project_job=response.body.job;
           this.projectid=data;
          },function(){
            alert("")
          })
        },

        bianji_project(){
          this.$http.put(this.path+"job/project/"+this.projectid+"/",{id:this.projectid,name:this.project_name,domain:this.project_domin,job:this.project_job},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);       
           this.project_list.forEach(element => {
             if(element.value==this.projectid){
               element.text=this.project_name;
             }
           });           
           
          },function(){
            alert("")
          })

        },

        create_project(){
           console.log(this.new_project_name);
           console.log(this.selected_1);
           console.log(this.selected_2);
           
           this.$http.post(this.path+"job/project/",{name:this.new_project_name,domain:this.selected_1,job:this.selected_2},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
             //  console.log(response);
             this.project_list.push({'text':this.new_project_name,'value':response.body.id})  
                this.information="";       
           },function(){
              alert("")
           });
        },

        delete_project(data){          
          this.project_delele_id=data;
         console.log(this.project_delele_id);
        },

        project_delele(){
          var data=this.project_delele_id;
           this.$http.delete(this.path+"job/project/"+data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
            this.project_list=this.project_list.filter(function(item){
              return item.value != data
            });            
          },function(){
            alert("")
          })
        },

         bianji_domain_information(data){
           this.$http.get(this.path+"paper/domain/"+data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);
           this.domain_id=response.body.id;
           this.domain_name=response.body.name;          
          },function(){
            alert("")
          })
        },

        bianji_domain(){
          this.$http.put(this.path+"paper/domain/"+this.domain_id+"/",{id:this.domain_id,name:this.domain_name,subject:window.localStorage.getItem(['user_subject'])},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
           console.log(response);       
           this.domin_list.forEach(element => {
             if(element.value==this.domain_id){
               element.text=this.domain_name;
             }
           });           
           
          },function(){
            alert("")
          })

        },

        create_domain(){
          
           console.log(this.new_domain_name);          
           this.$http.post(this.path+"paper/domain/",{name:this.new_domain_name,subject:window.localStorage.getItem(['user_subject'])},{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
             //  console.log(response);
             this.domin_list.push({'text':this.new_domain_name,'value':response.body.id})  
               this.information2=""        
           },function(){
              alert("")
           })                 

        },

        delete_domain(data){
          console.log(data);
          this.domain_delete_id=data;
         
        },

        domain_delete(){
          var data=this.domain_delete_id;
           this.$http.delete(this.path+"paper/domain/"+data+"/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
            this.domin_list=this.domin_list.filter(function(item){
              return item.value != data
            });            
          },function(){
            alert("")
          })
        }
     
    },

    mounted(){
      
          //

        this.$http.get(this.path+"job/projectrole/?user="+window.localStorage.getItem(['user_id']),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  if(response2.body.length==0){
                      this.information="";
                      console.log("")
                  }else{
                      var list=[];
                      console.log(response2.body);
                      response2.body.forEach(element => {
                          list.push({'text':element.name,'value':element.project})
                      });                      
                      this.project_list=list;
                  }

              },function(){
                alert("");
              }); 


            //
            if(this.user_role == 1){
              this.$http.get(this.path+"paper/subject/?id="+window.localStorage.getItem(['user_subject']),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  //
                  this.subject=response2.body[0].name;                 

              },function(){
                alert("");
              }); 

               this.$http.get(this.path+"job/job/",{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  //

                   var job=[];
                      console.log(response2.body);
                      response2.body.forEach(element => {
                          job.push({'text':element.name,'value':element.id,'job_table':element.job_table})
                      });                      
                      this.job_list=job;                 

              },function(){
                alert("");
              }); 


              this.$http.get(this.path+"paper/domain/?subject="+window.localStorage.getItem(['user_subject']),{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response2)=>{
                  console.log("",response2);
                  if(response2.body.length==0){
                      this.information2="";
                      console.log("")
                  }else{
                    //
                      var domin=[];
                      console.log(response2.body);
                      response2.body.forEach(element => {
                          domin.push({'text':element.name,'value':element.id})
                      });                      
                      this.domin_list=domin;
                  }

              },function(){
                alert("");
              }); 
            }
             
    }


}
</script>

<style>

#project_content{
  width:45%;
  height:auto;
 
  background-color:white;
   margin-left:6%;
  margin-top:3%;
  padding:30px;
  border-style:solid;
    border-width:2px;
}

#domain_content{
  width:40%;
 
  height:auto;
   margin-left:3%;
   margin-right:5%;
  background-color:white;
  margin-top:3%;
  padding:30px;
  border-style:solid;
    border-width:2px;
}



</style>



