<template>
<div id="findtext" > 
 <div class="find">
     <h6><b></b></h6>
     </br>

     <div>
   <b-form  inline @submit="onSubmit">   

    <label class="mr-sm-2" for="keyword" style="font-size:13px"></label>
    <b-form-input
      id="keyword"
      class="mb-2 mr-sm-4 mb-sm-0"
       v-model="form.keyword"
      placeholder=""
       style="font-size:13px;width:15%"
    ></b-form-input>   
   
    <b-button type="submit" variant="primary" style="font-size:13px;width:7%" ></b-button>
  </b-form>
</div>

</br>      
</br> 

<div class="table-responsive table-hover table-sm" style="height:auto;">
             <table class="table">
            
  <thead>
    <tr>
      <th scope="col"></th>
      <th scope="col" style="width:40%;text-align:center"></th>
      <th scope="col"></th>
      <th scope="col"></th> 
      <th scope="col" ></th>
    </tr>
  </thead>
  <tbody>   
    <tr v-for="item in items">
        <th scope="row">{{item.id}}</th>
        <td style="width:40%;text-align:center">{{item.name}}</td>
        <td>{{item.journal}}</td>
        <td>{{item.subject}}</td>
        <td> <b-button variant="danger" style="font-size:13px;margin:auto;" v-b-modal.modal-delete @click="deletepaper(item.paperid)">delete</b-button></td>
    </tr>

     <b-modal id="modal-delete" title="" @ok="paper_delete()" style="overflow:scroll;">

  <h6></h6>    
       
  </b-modal>
  
  </tbody>
            </table>
                </div>   

                <div>
        <nav aria-label="Page navigation example">
  <ul class="pagination justify-content-center">
    <li class="page-item"><a class="page-link" @click="pag('first')"></a></li>      
    <li v-for="n in limitpage"  v-if = "(currentpage+n)>0&&(currentpage+n)<=maxpage"  @click="pag(currentpage + n)"><span class="page-link" :style = "{color : n != 0 ? 'grey' : 'red'}">{{currentpage + n}}</span></li>
    <li class="page-item"><a class="page-link" @click="pag('end')"></a></li>
  </ul>
</nav>

    </div>      
     </div>
</div>
</template>


<script>
import configs from './configs.vue'
  export default {
      name: 'findtext',
       data() {
      return {
        path:configs.path,
          form:{
              // subject:null,
              // field:null,             
              keyword:""
          },    
          // fields:[],
          // subjects:[],   
          limitcontent:13,
          items: [],
          currentpage:1,
          maxpage:0,
          limitpage:[-2,-1,0,1,2],
          
          domain:window.localStorage.getItem('domain_id'),

          paper_delete_id:"",

      }
    },
     mounted(){

        var ID=1 
        var paper = []
        this.$http.get(this.path+"paper/paper/?domain="+this.domain+"&limit="+this.limitcontent,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log(response);      
            var pag =Math.ceil(response.body.count / this.limitcontent) ;             
           console.log("maxpage",pag);
             this.maxpage = pag;          
          response.body.results.forEach(element => {   
                 paper.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.paper_title,journal:element.journal,paperid:element.id,subject:element.subject_name});
                 ID=ID+1;             
          });
          this.items=paper;
          console.log("papers",this.items);

          },function(){
alert('');
});
},
 methods: {
      onSubmit(evt) {
        evt.preventDefault()
        console.log(JSON.stringify(this.form))
        this.pag("first");
     
        var ID=1 
        var paper = []
        this.$http.get(this.path+"paper/paper/?domain="+this.domain+"&search="+this.form.keyword+"&limit="+this.limitcontent,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {
console.log("search results",response);      
            var pag =Math.ceil(response.body.count / this.limitcontent) ;             
           console.log("maxpage",pag);
             this.maxpage = pag;       
             this.pag("first");   
          // response.body.results.forEach(element => {   
          //        paper.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.paper_title,journal:element.journal,paperid:element.id,subject:element.subject_name});
          //        ID=ID+1;             
          // });
          // this.items=paper;
          // console.log("papers",this.items);

          },function(){
alert('');
});
      },

       pag(data){
            console.log(this.currentpage);
            var off=0;
            var ID=1;
            if(data=="first"){
                off=0;
                this.currentpage=1;
            }else if(data == "end"){
                off=(this.maxpage-1)*this.limitcontent;
                this.currentpage=this.maxpage;
            }else{
                off = (data-1)*this.limitcontent;
                this.currentpage = data;
            }
            var pap = [];
              var path = this.path+"paper/paper/?domain="+this.domain+"&search="+this.form.keyword+"&limit="+this.limitcontent+"&offset="+off;
              this.$http.get(path,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response) => {      
                
                response.body.results.forEach(element => {       
                 pap.push({id:(this.currentpage-1)*this.limitcontent+ID,name:element.paper_title,journal:element.journal,paperid:element.id,subject:element.subject_name});
                 ID=ID+1;              
          });
           this.items=pap;              
            console.log("papers",this.items);    
            },function(){
                 alert("");
      });           
        },

        deletepaper:function(data){
          this.paper_delete_id=data;
        },

        paper_delete(){
          var data=this.paper_delete_id;
          this.$http.delete(this.path+"paper/paper/"+data,{headers:{Authorization:window.localStorage.getItem('Authorization')}}).then((response)=>{
            alert("");
            this.pag(this.currentpage);
          },function(){
            alert("");
          })
        }
 } 
      
  }
</script>

<style>

.findtext{
    height:100%;
    width:100%
}

.find{
  background-color:white;
  height:auto;
  width:auto;
  margin-left:2%;
  padding:2%  
}



</style>


