<template>
  <div id="navbar">
 <b-navbar variant="white" text-variant="black">
  
       <b-navbar-brand @click="rout('project')"><img src="../../static/logo.png" aria-hidden="true" style="height:30px"></img> <b></b></b-navbar-brand>
       
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>

    <b-navbar-nav style="font-size:18px;width=100%">  


      <b-nav-item-dropdown text="" right  v-show="entity">
        <b-dropdown-item @click="rout('entityannoation')"></b-dropdown-item>
        <b-dropdown-item href="#"></b-dropdown-item>
        <b-dropdown-item href="#"></b-dropdown-item>
      </b-nav-item-dropdown>

      <b-nav-item-dropdown text="" right  v-show="relation">
        <b-dropdown-item @click="rout('relation')"></b-dropdown-item>
        <b-dropdown-item href="#"></b-dropdown-item>
        <b-dropdown-item href="#"></b-dropdown-item>
      </b-nav-item-dropdown> 

    </b-navbar-nav>

     <b-navbar-nav style="font-size:18px;width=100%">
       <b-nav-item  @click="rout('allocate')"  v-show="user_role == 1"> 
       </b-nav-item>
       </b-navbar-nav>

    <b-navbar-nav class="ml-auto">
        <b-button variant="dark" pill size="sm" class="my-sm-0" @click="rout('project')" >  </b-button>&nbsp;
         <b-button variant="secondary" pill size="sm" class="my-sm-0" @click="quit()" > {{username}}  </b-button> 
      </b-navbar-nav>            
  
   </b-collapse>

   </b-navbar>
</div>
</template>

<script>
import configs from './configs.vue'
  export default {
      name: 'navbar',
      props:['entity','relation'],
      data(){
        return{
           path:configs.path,
           user_role:window.localStorage.getItem('user_role'),
           username: window.localStorage.getItem('user'),              
        }
      },
      methods:{

        quit(){
          window.localStorage.removeItem('token');
          this.$router.push({path:'/'})
        },

        rout(data){
          this.$router.push({
            path:'/'+data,
          })
        },
      

      },
      mounted(){      
      }
    
  }
</script>

<style>


</style>


